import {
  Card,
  CardContent,
  Grid,
  Typography,
  useTheme,
} from "@material-ui/core";
import React from "react";
import FlipCard from "../FlipCard/FlipCard";
import LikeButton from "../LikeButton/LikeButton";
import ScrollTop from "../ScrollTop/ScrollTop";

const Cell = (props) => {
  const { data, height, width, searchMovie } = props;
  const theme = useTheme();
  return (
    <div style={{ marginLeft: 10, marginRight: 10 }}>
      <FlipCard
        height={height}
        width={width}
        front={
          <>
            <img
              // onClick={() => {
              //   searchMovie(movie);
              // }}
              style={{ height: height, width: width }}
              src={data?.posterPath}
              alt="Poster"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src =
                  "data:image/png;base64,";
              }}
            />
          </>
        }
        back={
          <Card style={{ height: height, width: width }}>
            <CardContent>
              <Grid container spacing={1}>
                <Grid item xs={12}>
                  <Typography>Genres:</Typography>
                  {data?.genres?.split("|").map((value, key) => (
                    <Typography key={key} variant="body2">
                      {value}
                    </Typography>
                  ))}
                </Grid>

                {/* <Grid item xs={12}>
                  <Typography>Popularity</Typography>
                  <Typography variant="body2">{data?.popularity}</Typography>
                </Grid> */}
                <Grid item xs={12}>
                  <Typography>IMDB:</Typography>
                  <Typography variant="body2">{data?.imdbRating}</Typography>
                </Grid>

                <Grid item xs={12}>
                  <Typography>Director:</Typography>
                  <Typography variant="body2">{data?.Director}</Typography>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        }
      />

      <Grid
        container
        style={{
          width: width,
          background: theme.palette.primary.main,

          borderRadius: "0px 0px 10px 10px",
        }}
      >
        <Grid item xs={8}>
          <ScrollTop>
            <Typography
              variant="body1"
              onClick={() => {
                searchMovie(data);
              }}
              style={{ padding: 20, cursor: "pointer", overflow: "hidden" }}
            >
              {data?.title ?? "Unknown"}
            </Typography>
          </ScrollTop>
        </Grid>
        <Grid item xs={4} style={{ padding: 10 }}>
          <LikeButton index={data?.index ?? 0} height={20} width={20} />
        </Grid>
      </Grid>
    </div>
  );
};

export default Cell;
