import { Typography, makeStyles, Grid, Paper } from "@material-ui/core";
import React from "react";
import Skeleton from "@material-ui/lab/Skeleton";
import LikeButton from "../LikeButton/LikeButton";

// const style = {
//     width: 200,
//     height: 200,
//     borderRadius: 100,
//     objectFit: 'cover'
// }
const useStyles = makeStyles((theme) => ({}));

const MovieDetail = (props) => {
  const { movie, loading = false } = props;
  const classes = useStyles();

  return (
    <Paper>
      <Grid container>
        <Grid item container md={4} lg={2} alignItems="center" justify="center">
          {loading ? (
            <Skeleton animation="wave" variant="rect" />
          ) : (
            <Grid item>
              <img
                alt="Movie Poster"
                className={classes.media}
                src={movie?.Poster}
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src =
                    "data:image/png;base64,";
                }}
              />
            </Grid>
          )}
        </Grid>

        <Grid
          container
          direction="row"
          item
          md={8}
          lg={10}
          style={{ padding: 20 }}
        >
          <Grid
            container
            item
            sm={5}
            md={3}
            alignItems="center"
            justify="center"
            direction="row"
            spacing={8}
            style={{ padding: 20 }}
          >
            {loading ? (
              <>
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  width="80%"
                  style={{ marginBottom: 6 }}
                />
              </>
            ) : (
              <Grid item>
                <Grid item>
                  <Typography variant="h6">Release Date:</Typography>
                  <Typography variant="body2">
                    {movie?.Year ?? "Unknown"}
                  </Typography>
                  <Grid item>
                    <Typography variant="h6">Runtime:</Typography>

                    <Typography variant="body2">
                      {movie?.Runtime ?? "Unknown"}
                    </Typography>
                  </Grid>

                  <Grid item>
                    <Typography variant="h6">Genre:</Typography>
                    <Typography variant="body2">
                      {movie?.Genre ?? "Unknown"}
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item container>
                  <Typography variant="h6">Ratings:</Typography>
                  {movie?.Ratings?.map((Ratings, index) => {
                    return (
                      <Grid item xs={12} key={Ratings?.Source ?? index}>
                        <Typography variant="body2">
                          {Ratings?.Source ?? "Unknown"}:{" "}
                          {Ratings?.Value ?? index}
                        </Typography>
                      </Grid>
                    );
                  })}
                </Grid>
              </Grid>
            )}
          </Grid>

          <Grid
            container
            item
            sm={7}
            md={9}
            alignItems="center"
            justify="center"
            spacing={8}
          >
            {loading ? (
              <Grid item>
                <Skeleton
                  animation="wave"
                  height={10}
                  style={{ marginBottom: 6 }}
                />
                <Skeleton
                  animation="wave"
                  height={10}
                  width="80%"
                  style={{ marginBottom: 6 }}
                />
              </Grid>
            ) : (
              <Grid item container>
                <Grid item container xs={12}>
                  <Grid item>
                    <Typography variant="h3">{movie?.Title}</Typography>
                  </Grid>
                  <Grid item>
                    <LikeButton
                      index={movie?.index ?? 0}
                      height={40}
                      width={40}
                    />
                  </Grid>
                </Grid>

                <Grid item xs={12}>
                  <Typography variant="h6">Actors:</Typography>
                  <Typography variant="body2">
                    {movie?.Actors ?? "Unknown"}
                  </Typography>
                </Grid>

                <Grid item xs={12}>
                  <Typography variant="h6">Box Office:</Typography>
                  <Typography variant="body2">
                    {movie?.BoxOffice ?? "Unknown"}
                  </Typography>
                </Grid>

                <Grid item xs={12}>
                  <Typography variant="h6">Plot:</Typography>
                  <Typography variant="body2" align="justify">
                    {movie?.Plot ?? "Unknown"}
                  </Typography>
                </Grid>
              </Grid>
            )}
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default React.memo(MovieDetail);
